@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@400;600;700&family=Roboto+Condensed:wght@300;400&display=swap);
html {
  height: 100vh;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-height: 100vh;
  height: 100vh;
}
.display-container {
  height: 100vh;
  background-color: rgb(39, 40, 34);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.half-width {
  display: inline-block;
  position: relative;
  width: 60%;
  height: 100vh;
  overflow-y: scroll;
  transition: all 0.2s linear;
}
.half-width.json-source {
  width: 40%;
  padding: 10px;
  background-color: rgb(39, 40, 34);
  font-size: 12px;
}

.half-width.full {
  width: 100%;
}
.half-width.shrink {
  display: none;
  width: 0%;
}

.btn-group-toggle {
  background: #fff;
  border-bottom: 1px solid #ddd;
  line-height: 12px;
}

.btn-group-toggle .title,
.btn-group-toggle button {
  font-size: 12px;
  font-weight: bold;
  display: inline-block;
}

.btn-group-toggle .title {
  padding: 4px 10px;
  background: #009688;
  color: #fff;
}

.btn-group-toggle img {
  height: 100%;
}

.btn-group-toggle input,
.btn-group-toggle button {
  padding: 4px 10px;
  float: right;
  border: none;
  border-left: 1px solid #ddd;
  cursor: pointer;
}

.btn-group-toggle input {
  padding: 1px;
  cursor: text;
  width: 40px;
  text-align: center;
}

/* add css module styles here (optional) */

.arf-leaflet {
  margin-top: 10px;
  height: 300px;
  width: 100%;
}

.arf-container {
  position: relative;
  font-family: 'Roboto Condensed', sans-serif;
  background: #fff;
}

.arf-form-header {
  padding: 0px 20px;
  background: #fff;
}
.arf-form-header.arf-sticky {
  top: 0px;
  position: sticky;
  position: -webkit-sticky;
  min-width: 100%;
  z-index: 1001;
  border-bottom: 1px solid #f0f0f0;
}
.arf-form-header h1 {
  font-family: 'Assistant', sans-serif;
  line-height: 58px;
  padding: 0px;
  margin: 0px;
  font-size: 18px;
  font-weight: bold;
}

.arf-form-header h1,
.arf-field-group .arf-field-group-header,
.arf-sidebar .arf-sidebar-list.arf-active,
.arf-sidebar .arf-sidebar-header {
  color: #6a6a6a;
}

.arf-form-header button {
  float: right;
}

.arf-sidebar {
  border-top: 1px solid #f0f0f0;
}

.arf-sidebar.arf-sticky {
  border: none;
  top: 60px;
  position: sticky;
  position: -webkit-sticky;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.arf-sidebar .arf-sidebar-header {
  text-transform: uppercase;
}

.arf-sidebar .arf-sidebar-list {
  cursor: pointer;
  font-family: 'Assistant', sans-serif;
  padding: 10px 20px;
  font-size: 14px;
}

.arf-sidebar .arf-sidebar-list.arf-active {
  background: #f3f3f3;
  font-weight: bold;
}

.arf-sidebar .arf-sidebar-list.arf-complete {
  color: green;
}

.arf-sidebar .arf-sidebar-list .arf-icon {
  margin-right: 10px;
  padding: 0px;
  margin-bottom: -2px;
}

.arf-field-group .arf-field-group-header,
.arf-sidebar-header {
  font-family: 'Assistant', sans-serif;
  font-size: 14px;
  font-weight: bold;
  padding: 0px 20px;
}
.arf-field-group p.arf-description {
  font-family: 'Assistant', sans-serif;
  font-size: 14px;
  margin: 0px 20px 20px;
}

.arf-field-group .arf-field {
  font-family: 'Assistant', sans-serif;
  padding: 0px 20px 10px;
}

.arf-field.arf-field-geo {
  display: none !important;
}

.arf-field.arf-field-map {
  margin-top: -65px;
}

.arf-field-group.arf-hidden {
  display: none;
}

.arf-next {
  border-left: 1px solid #f0f0f0;
  padding: 20px 50px;
}
.arf-next button {
  border: none;
  background-color: #888;
  color: #fff;
}

.arf-next button:hover {
  background-color: #222;
  color: #fff;
}

